import NextHead from 'next/head'
import { Component } from 'react'

/*
  TODO:
  atom.png  
  meta  
*/


export default class SeoHead extends Component {
  render() {
    const {
      title = 'peopet',
      description,
      image,
      children,
      url,
    } = this.props
    return (
      <NextHead>
        <title>
          {title}
        </title>
        {/* <meta name="description" content={description} /> */}
        <meta name="description" content=", ,  , ,  ,   , , , " />
        <meta property="keywords" content=", ,  , ,  ,   , , , " />

        <link itemProp="url" href="http://peopet.co.kr/" />
        <meta itemProp="name" content={title} />
        <meta itemProp="description" content={description} />
        <meta itemProp="image" content="/static/atom.png" />

        <meta
          name="google-site-verification"
          content="kC4Y5cVvYNKPj1kleZi45vi1vZdKgN3bvdQKUYkyBFE"
        />

        <meta property="og:locale" content="ko_KR" />
        <meta property="og:type" content="website" />
        {/* <meta property="og:title" content={title} /> */}
        <meta property="og:title" content=" |   | peopet |   | " />
        {/* <meta property="og:url" content={url} /> */}
        <meta property="og:url" content="http://peopet.co.kr" />
        <meta property="og:image" content={image} />
        <meta property="og:image:height" content="652" />
        <meta property="og:image:width" content="652" />
        {/* <meta property="og:description" content={description} /> */}
        <meta property="og:description" content=", ,  , ,  ,   , , , " />
        <meta property="og:site_name" content="_peopet" />

        <link rel="canonical" href="http://peopet.co.kr" />

        {children}

        <link rel="shortcut icon" href="/static/pavicon.png" />
        <link rel="icon" href="/static/pavicon.png" />
      </NextHead>
    )
  }
}



